p,
ul,
ol {
  font-family: 'Lora', serif;
  font-size: 18px;
  line-height: 26px;
}

@media screen and (max-width: 640px) {
  p,
  ul,
  ol {
    font-size: 14px;
    line-height: 22px;
  }
}

h2 {
  margin-top: 0;
  margin-bottom: 36px;
  font-family: 'Sacramento', cursive;
  font-size: 42px;
  text-align: center;
  font-weight: 400;
}

@media screen and (max-width: 640px) {
  h2 {
    font-size: 32px;
    margin-bottom: 22px;
  }
}

@media screen and (max-width: 420px) {
  h2 {
    margin-top: 20px;
    margin-bottom: 24px;
  }
}

.headline-break {
  display: none;
}

@media screen and (max-width: 470px) {
  .headline-break {
    display: inherit;
  }
}

body {
  background: #ddd url(../images/bg.svg) no-repeat center -56px;
  background-size: 4096px 1152px;
  color: #2a2829;
  margin: 0;
}

@media screen and (max-width: 1050px) {
  body {
    background-position: center -35px;
    background-size: 2560px 720px;
  }
}

@media screen and (max-width: 420px) {
  body {
    background-position: center -28px;
    background-size: 2048px 576px;
  }
}

header {
  position: relative;
  height: 256px;
  overflow: auto;
  text-align: center;
}

@media (max-width: 1050px) {
  header {
    height: 160px;
  }
}

@media (max-width: 420px) {
  header {
    height: 128px;
  }
}

main {
  box-sizing: border-box;
  max-width: 960px;
  min-height: 960px;
  margin: 0 auto 40px;
  padding: 80px 140px 80px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2), inset 0 0 10px rgba(0, 0, 0, 0.03);
}

@media screen and (max-width: 1050px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 30px 40px;
    padding: 60px;
  }
}

@media screen and (max-width: 640px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 20px 40px;
    padding: 40px;
  }
}

@media screen and (max-width: 420px) {
  main {
    width: calc(100% - ($margin-side * 2));
    margin: 0 10px 40px;
    padding: 20px;
  }
}

.monogram {
  position: relative;
  margin: 0;
  top: 59px;
}

@media (max-width: 1050px) {
  .monogram {
    top: 35px;
  }
}

@media (max-width: 420px) {
  .monogram {
    top: 26px;
  }
}

.monogram__image {
  width: 200px;
}

@media (max-width: 1050px) {
  .monogram__image {
    width: 130px;
  }
}

@media (max-width: 420px) {
  .monogram__image {
    width: 110px;
  }
}

footer {
  margin: 0 0 40px;
  text-align: center;
  color: #9d9d9d;
  text-shadow: 0 1px 0 white;
}

.browserupgrade {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}
